<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-select size="small" v-model="formInline.deptName" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option v-for="(item, index) in deptNameInfoList" :key="index" :value="item">{{item}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.license_plate" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
<!--        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>-->
<!--        <el-button size="small" type="primary" @click="handleunit()"></el-button>-->
      </el-form-item>
    </el-form>

<!--    <div style="width: 298px;text-align: center">-->
<!--      <video style="width:950px" poster="https://cdn.jsdelivr.net/gh/themusecatcher/resources@0.0.3/bao.jpg" controls>-->
<!--      <video style="width:950px" controls>-->
<!--        &lt;!&ndash;            <source src="../../assets/img/1.mp4" type="video/mp4" >&ndash;&gt;-->
<!--        <source :src="videoUrl" type="video/mp4">-->
<!--      </video>-->
    <video style="width:950px" controls v-bind:src="videoUrl"></video>

    <p>
      <el-tag></el-tag>
    </p>
    <!---->
    <el-table size="small" @selection-change="selectChange" :data="visibleVideoInfoList" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="videoName" label="" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="" width="80">
      </el-table-column>
      <el-table-column align="center" sortable prop="username" label="" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="phone" label="" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="license_plate" label="" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="starttime" label="" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="duration" label="" min-width="80">
      </el-table-column>
      <el-table-column align="center" sortable prop="accidentAddress" label="" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="type" label="" min-width="80">
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.type == 1"></el-tag>
          <el-tag v-if="scope.row.type == 2"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
<!--          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>-->
<!--          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>-->
<!--            <el-button size="mini" type="success" :loading="scope.row.loadin0g" @click="startPlay(scope.$index, scope.row)">
          {{ scope.row.loading ? '' : '' }}
        </el-button>  -->
          <el-button v-if="scope.$index==currentIndex" size="mini" type="danger" @click="startPlay(scope.$index, scope.row)"></el-button>
          <el-button v-else size="mini" type="success" @click="startPlay(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="download(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="currentVideoInfoList.length">
    </el-pagination>

    <!--  -->
<!--    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>-->
<!--    v-bind:child-msg="pageparm": Vue.jsv-bind:HTMLchild-msgpageparmpageparmVue
@callFather="callFather": Vue.js@DOMcallFatherVuecallFather-->

  </div>
</template>


<script>
import Pagination from '../../components/Pagination'
import axios from "axios";
import index from "vuex";

export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      loading: false, //
      currentIndex:-1,
      // 
      formInline: {
        deptName: '',//
        phone: '',
        license_plate: '',
      },
      videoInfoList:[],
      currentVideoInfoList:[],
      deptNameInfoList:[],
      videoUrl:'http://101.34.13.144:8080/examples/2023-11-08 20-04-27_18345264895.mov' ,
    }

  },
  components: {
    Pagination
  },
  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.videoList()
    this.deptNameList()
  },

  computed: {
    index() {
      return index
    },
    //
    visibleVideoInfoList() {

      let result = this.videoInfoList;
      // const startIndex = (this.currentPage - 1) * this.pageSize;
      // const endIndex = this.currentPage * this.pageSize;
      // let result = this.videoInfoList.slice(startIndex, endIndex);

      if (this.formInline.deptName) {
        // 
        result = result.filter(video => video.deptName === this.formInline.deptName);
      }
      if (this.formInline.phone) {
        // 
        result = result.filter(video => video.phone === this.formInline.phone);
      }
      if (this.formInline.license_plate) {
        // 
        result = result.filter(video => video.license_plate === this.formInline.license_plate);
      }

      this.currentVideoInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    }
  },

  /**
   * 
   */
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    // 
    startPlay: function(index, row) {

      this.currentIndex=index
      this.videoUrl=row.videoUrl
      console.log("this.videoUrl:"+this.videoUrl)
    },
    // 
    download:function (index, row) {
      // 
      window.location.href = row.videoUrl;
    },

    deptNameList(){
      this.videoInfoList.forEach(obj => {
        if(!this.deptNameInfoList.includes(obj.deptName)) {
          this.deptNameInfoList.push(obj.deptName);
        }
      });
      console.log("this.deptNameInfoList.length"+this.deptNameInfoList.length+"this.deptNameInfoList"+this.deptNameInfoList[0])
    },
    videoList(){
      console.log("videoList()")
      this.loading = true
      const that = this;
      axios({
        method: 'post',
        url: '/Track/get_video_info_list',
      }).then((response) => {
        if (response.data != null){
          that.videoInfoList=response.data
          console.log("response.data:"+response.data)
          console.log("response.data.length():"+response.data.length)
          that.deptNameList();
        }else{
          console.log("")
          //
          this.$message({
            type: 'error',
            message: ''
          })
        }
      })
      this.loading = false
    },
    // 
    selectChange(val) {
      this.selectdata = val
    },
    //
    search() {
      console.log("formInline:"+this.formInline.deptName+":"+this.formInline.phone+":"+this.formInline.license_plate)


    },
  }
}
</script>


<style scoped>
.user-search {
  margin-top: 20px;
}

.userRole {
  width: 100%;
}
</style>



