<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
<!--            <el-select size="small" v-model="formInline.status" placeholder=""  >
          <el-option label="" value=""></el-option>
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>-->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-select size="small" v-model="formInline.deptName" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option v-for="(item, index) in deptNameInfoList" :key="index" :value="item">{{item}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.license_plate" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
        <el-button size="small" type="primary" @click="handleunit()"></el-button>
      </el-form-item>
    </el-form>

    <!---->
    <el-table size="small" @selection-change="selectChange" :data="visibleTruckInfoList" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="username" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="phone" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="license_plate" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="sex" label="" min-width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="status:" label="" min-width="50">
        <template slot-scope="scope">
          <el-tag :type="getStatusType(scope.row.status)">
            {{ getStatusText(scope.row.status) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column align="center" sortable prop="editTime" label="" min-width="120">
        <template slot-scope="scope">
          <div>{{scope.row.editTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="resetpwd(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="offlineUser(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>

    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="currentTrackInfoList.length">
    </el-pagination>
  </div>
</template>

<script>
import Pagination from '../../components/Pagination'
import axios from "axios";
export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      loading: false, //
      // 
      formInline: {
        deptName: '',//
        phone: '',
        license_plate: '',
      },
      trackInfoList:[],
      currentTrackInfoList:[],
      deptNameInfoList:[],
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.trackList()
  },


  computed: {
    index() {
      return index
    },
    //
    visibleTruckInfoList() {

      let result = this.trackInfoList;


      if (this.formInline.deptName) {
        // 
        result = result.filter(video => video.deptName === this.formInline.deptName);
      }
      if (this.formInline.phone) {
        // 
        result = result.filter(video => video.phone === this.formInline.phone);
      }
      if (this.formInline.license_plate) {
        // 
        result = result.filter(video => video.license_plate === this.formInline.license_plate);
      }

      this.currentTrackInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    },
  },
  /**
   * 
   */
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    deptNameList(){
      this.trackInfoList.forEach(obj => {
        if(!this.deptNameInfoList.includes(obj.deptName)) {
          this.deptNameInfoList.push(obj.deptName);
        }
      });
      console.log("this.deptNameInfoList.length"+this.deptNameInfoList.length+"this.deptNameInfoList"+this.deptNameInfoList[0])
    },
    //
    search() {
      this.getdata(this.formInline)
    },
    getStatusText(status) {
      return status == 0 ? '' : status == 1 ? '' : '';
    },
    getStatusType(status) {
      return status == 0 ? 'info' : status == 1 ? 'success' : 'danger';
    },
    trackList(){
      console.log("")
      this.loading = true
      var that =this
      axios({
        method: 'post',
        url: '/Track/get_track_info_list',
      }).then((response) => {
        if (response.data != null){
          that.trackInfoList=response.data
          that.deptNameList();
        }else{
          console.log("")
          //
          this.$message({
            type: 'error',
            message: ''
          })
        }
      })
      this.loading = false
    },
    // 
    selectChange(val) {
      this.selectdata = val
    },
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>




