<script>
import Pagination from '../../components/Pagination'
import axios from "axios";
import car from '@/assets/img/car.jpg'

export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      loading: false, //
      // 
      formInline: {
        deptName: '',//
        phone: '',
        license_plate: '',
      },
      currentIndex: -1,
      base64Image: car,
      // 
      IPaddress: '',
      WebSocketIP: '',
      socket: '',
      trackInfoList: [],
      currentTrackInfoList:[],
      deptNameInfoList:[],
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.trackList()
    this.getdata(this.formInline)
  },
  computed: {
    index() {
      return index
    },
    //
    visibleTruckInfoList() {

      let result = this.trackInfoList;


      if (this.formInline.deptName) {
        // 
        result = result.filter(video => video.deptName === this.formInline.deptName);
      }
      if (this.formInline.phone) {
        // 
        result = result.filter(video => video.phone === this.formInline.phone);
      }
      if (this.formInline.license_plate) {
        // 
        result = result.filter(video => video.license_plate === this.formInline.license_plate);
      }

      this.currentTrackInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    },
  },
  /**
   * 
   */
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    deptNameList(){
      this.trackInfoList.forEach(obj => {
        if(!this.deptNameInfoList.includes(obj.deptName)) {
          this.deptNameInfoList.push(obj.deptName);
        }
      });
      console.log("this.deptNameInfoList.length"+this.deptNameInfoList.length+"this.deptNameInfoList"+this.deptNameInfoList[0])
    },

    // 
    stop: function () {
      if (this.socket.readyState === WebSocket.OPEN)
        this.socket.close();
      this.currentIndex=-1;
    },
    // 
    detection: function (index, row) {
      console.log("row.status:"+row.status)
      if (row.status=='0'){
        this.$message.error('!!');
        return;
      }
      this.currentIndex=index;
      this.IPaddress = row.ipaddress
      console.log("this.IPaddress:" + this.IPaddress)
      this.WebSocketIP = "ws://" + this.IPaddress + ":6010/websocket";
      console.log("this.WebSocketIP:" + this.WebSocketIP)
      if (this.socket.readyState === WebSocket.OPEN)
        this.socket.close();
      this.socket = new WebSocket(this.WebSocketIP);
      this.socket.onopen = function () {
        // Web Socket  send() 
        this.socket.send("")
        alert("...")
      }

      // WebSocket
      this.socket.onmessage = (event) => {
        console.log("event.data:" + event.data);
        console.log("event.data:" + event.data.toString());
        console.log("this.videoContent:");

        const base64Data = event.data; // Base64
        this.base64Image = 'data:image/jpg;base64,' + base64Data;

      };
    },
    //
    search() {

    },
    getStatusText(status) {
      return status == 0 ? '' : status == 1 ? '' : '';
    },
    getStatusType(status) {
      return status == 0 ? 'info' : status == 1 ? 'success' : 'danger';
    },
    trackList() {
      console.log("")
      this.loading = true
      var that = this
      axios({
        method: 'post',
        url: '/Track/get_track_info_list',
      }).then((response) => {
        if (response.data != null) {
          that.trackInfoList = response.data
          that.deptNameList();
        } else {
          console.log("")
          //
          this.$message({
            type: 'error',
            message: ''
          })
        }
      })
      this.loading = false
    },
    // 
    selectChange(val) {
      this.selectdata = val
    },
  },

  mounted() {
    // 
    // this.WebSocketIP = "ws://192.168.29.76:6010/websocket";
    // console.log("this.WebSocketIP:" + this.WebSocketIP)
    // if (this.socket.readyState === WebSocket.OPEN)
    //   this.socket.close();
    // this.socket = new WebSocket(this.WebSocketIP);
    // this.socket.onopen = function () {
    //   // Web Socket  send() 
    //   this.socket.send("")
    //   alert("...")
    // }
    //
    // // WebSocket
    // this.socket.onmessage = (event) => {
    //   console.log("event.data:" + event.data);
    //   console.log("event.data:" + event.data.toString());
    //   console.log("this.videoContent:");
    //
    //   const base64Data = event.data; // Base64
    //   this.base64Image = 'data:image/jpg;base64,' + base64Data;
    //
    // };

    // WebSocket
    // console.log("mounted()WebSocketIP:"+this.WebSocketIP)
    // // ws://192.168.29.76/websocket
    // // const socket = new WebSocket('ws://192.168.43.216:6010/websocket');
    // this.socket = new WebSocket(this.WebSocketIP);
    // // const socket = new WebSocket('ws://192.168.29.76:6010/websocket');
    // // 
    // this.socket.onopen = function () {
    //   // Web Socket  send() 
    //   this.socket.send("")
    //   alert("...")
    // }
    //
    // // WebSocket
    // this.socket.onmessage = (event) => {
    //   console.log("event.data:" + event.data);
    //   console.log("event.data:" + event.data.toString());
    //   console.log("this.videoContent:");
    //
    //   const base64Data = event.data; // Base64
    //   this.base64Image = 'data:image/jpg;base64,' + base64Data;
    //
    // };
  },
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}

.userRole {
  width: 100%;
}
</style>




