<template>
  <div class="login-wrap">
    <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px"
             class="demo-ruleForm login-container">
      <h3 class="title"></h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="ruleForm.username" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <!--      -->
      <el-row>
        <el-col :span="12">
          <el-form-item prop="code">
            <el-input type="text" v-model="ruleForm.code" auto-complete="off" placeholder=""
                      @keyup.enter.native="submitForm('ruleForm')"></el-input>
          </el-form-item>
        </el-col>
        <el-col  :span="12" class="code-box">
          <div class="login-code" width="100%" @click="refreshCode">
            <!---->
            <v-sidentify></v-sidentify>
          </div>
          <!--          <img :src="ruleForm.codeimg" alt="" class="codeimg" @click="getcode()">-->
        </el-col>
      </el-row>
      <el-checkbox class="remember" v-model="rememberpwd"></el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining">
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
import axios from "axios";
import {setCookie, getCookie, delCookie} from '../utils/util'
import md5 from 'js-md5'
import Sidentify from "../components/v-sidentify.vue";

export default {
  name: 'login',
  data() {
    return {
      //loadingfalse
      logining: false,
      // 
      rememberpwd: false,
      identifyCodes: "1234567890abcdefjhijklinopqrsduvwxyz", //,
      identifyCode: "", //
      ruleForm: {
        //usernamepassword
        username: '',
        password: '',
        code: '',
        randomStr: '',//
        codeimg: '',//
        sidentify: '',
      },
      //rules:rules="rules"  rules
      // 
      rules: {
        username: [{required: true, message: '', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}],
        code: [{required: true, message: '', trigger: 'blur'}]
      }
    }
  },
  components: {
    'v-sidentify': Sidentify
  },
  // ()
  created() {

    this.$message({
      message: '',
      type: 'success'
    })
    // 
    this.createCode()
    // 
    this.getuserpwd()

  },
  mounted() {
    // 
    this.identifyCode = "";
    //12
    this.makeCode(this.identifyCodes, 4);
  },
  // 
  methods: {

    // 
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    //
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        //
        this.identifyCode +=
          this.identifyCodes[this.randomNum(0, this.identifyCodes.length)];
      }
    },
    //
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    //
    // getcode() {
    //   // 
    //   this.ruleForm.code = Math.random().toString(36).substr(2, 4);
    //   //  src
    //   this.ruleForm.codeimg = 'data:image/png;base64,' + btoa(this.ruleForm.code);
    // },
    // 
    getuserpwd() {
      if (getCookie('user') != '' && getCookie('pwd') != '') {
        this.ruleForm.username = getCookie('user')
        this.ruleForm.password = getCookie('pwd')
        this.rememberpwd = true
      }
    },
    //info
    // formName
    submitForm(formName) {
      //$refs HTML this.$refs[formName]formName
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logining = true
          // 
          setTimeout(() => {
            this.logining = false
            this.$store.commit('login', 'true')
            this.$router.push({path: '/goods/Goods'})
          }, 1000)
        }
          // 
          login(this.ruleForm).then(res => {
            if (res.success) {
              if (this.rememberpwd) {
                //cookie7
                setCookie('user', this.ruleForm.username, 7)
                //cookie7
                setCookie('pwd', this.ruleForm.password, 7)
              } else {
                delCookie('user')
                delCookie('pwd')
              }
              //2
              setTimeout(() => {
                this.logining = false
                // token
                localStorage.setItem('logintoken', res.data.token)
                // 
                localStorage.setItem('userdata', JSON.stringify(res.data))
                this.$store.commit('login', 'true')
                this.$router.push({ path: '/goods/Goods' })
              }, 1000)
            } else {
              this.$message.error(res.msg)
              this.logining = false
              return false
            }
          })
        //   var that = this
        //   axios({
        //     method: 'post',
        //     url: '/User/get_track_info_list',
        //   }).then((response) => {
        //     if (response.data != null) {
        //       if (this.rememberpwd) {
        //         //cookie7
        //         setCookie('user', this.ruleForm.username, 7)
        //         //cookie7
        //         setCookie('pwd', this.ruleForm.password, 7)
        //       } else {
        //         delCookie('user')
        //         delCookie('pwd')
        //       }
        //
        //
        //     } else {
        //       this.$message.error(res.msg)
        //       this.logining = false
        //       return false
        //     }
        //   })
        // } else {
        //   // 
        //   this.getcode()
        //   this.$message.error('')
        //   this.logining = false
        //   return false
        // }
      })
    },
  }
}
</script>

<style scoped>
.login-wrap {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 10%;
  background-image: url(data:image/svg+xml;base64,);
  /* background-color: #112346; */
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 100%;
}

.login-container {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.remember {
  margin: 0px 0px 35px 0px;
}

.code-box {
  text-align: right;
}

.codeimg {
  height: 40px;
}
</style>



