/**
 *   
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">

      <el-form-item label="">
        <el-input size="small" v-model="formInline.role_name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.role_id" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="visibleRoleInfoList" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="role_name" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="role_id" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="create_time" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="create_name" label="" width="100">
      </el-table-column>
<!--      <el-table-column sortable prop="editUser" label="" width="300">-->
<!--      </el-table-column>-->
      <el-table-column align="center" label="" min-width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="menuAccess(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="currentRoleInfoList.length">
    </el-pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="120px" :model="editForm" ref="editForm" :rules="rules">
        <el-form-item label="" prop="systemNo">
          <el-input size="small" v-model="editForm.systemNo" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleName">
          <el-input size="small" v-model="editForm.roleName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleNo">
          <el-input size="small" v-model="editForm.roleNo" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="menuAccessshow" width="30%" @click='closeDialog("perm")'>
      <el-tree ref="tree" default-expand-all="" :data="RoleRight" :props="RoleRightProps" :default-checked-keys="checkmenu" node-key="id" show-checkbox>
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("perm")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="menuPermSave"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  roleList,
  roleSave,
  roleDelete,
  rolePwd,
  RoleRightTree,
  RoleRightSave
} from '../../api/userMG'
import Pagination from '../../components/Pagination'
import index from "vuex";
import axios from "axios";
export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      // 
      formInline: {
        role_name: '',
        role_id: '',
      },
      roleInfoList:[],
      currentRoleInfoList:[],
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      menuAccessshow: false, //
      title: '',
      editForm: {
        roleId: '',
        systemNo: '',
        roleNo: '',
        roleName: '',
        token: localStorage.getItem('logintoken')
      },
      // rules 
      rules: {
        systemNo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleNo: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      RoleRight: [],
      RoleRightProps: {
        children: 'children',
        label: 'name'
      },
      // 
      checkmenu: [],
      //role
      saveroleId: '',
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  watch: {},

  /**
   * 
   */

  created() {
    this.roleList()
  },

  computed: {
    index() {
      return index
    },
    //
    visibleRoleInfoList() {
      let result = this.roleInfoList;
      // const startIndex = (this.currentPage - 1) * this.pageSize;
      // const endIndex = this.currentPage * this.pageSize;
      // let result = this.videoInfoList.slice(startIndex, endIndex);

      if (this.formInline.role_id) {
        // 
        result = result.filter(role => role.role_id === this.formInline.role_id);
      }
      if (this.formInline.role_name) {
        // 
        result = result.filter(role => role.role_name === this.formInline.role_name);
      }
      this.currentRoleInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    }
  },
  /**
   * 
   */

  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    // 
    roleList() {
      console.log("")
      this.loading = true
      var that = this
      axios({
        method: 'post',
        url: '/User/get_role_info_list',
      }).then((response) => {
        if (response.data != null) {
          that.roleInfoList = response.data
        } else {
          console.log("")
          //
          this.$message({
            type: 'error',
            message: ''
          })
        }
      })
      this.loading = false
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.roleId = row.roleId
        this.editForm.systemNo = row.systemNo
        this.editForm.roleNo = row.roleNo
        this.editForm.roleName = row.roleName
      } else {
        this.title = ''
        this.editForm.roleId = ''
        this.editForm.systemNo = ''
        this.editForm.roleNo = ''
        this.editForm.roleName = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          roleSave(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.success) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    deleteUser(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          roleDelete(row.roleId)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: ''
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    menuAccess: function(index, row) {
      this.menuAccessshow = true
      this.saveroleId = row.roleId
      RoleRightTree(row.roleId)
        .then(res => {
          if (res.data.success) {
            this.$message({
              type: 'success',
              message: ''
            })
            this.changemenu(res.data.data)
            this.RoleRight = this.changeArr(res.data.data)
          } else {
            this.$message({
              type: 'info',
              message: res.data.msg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    changemenu(arr) {
      let change = []
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].checked) {
          change.push(arr[i].id)
        }
      }
      this.checkmenu = change
    },
    // tree 
    changeArr(list) {
      var temptree = [],
        tree = [],
        items = []
      for (var i in list) {
        if (!temptree[list[i].id]) {
          var trow = {
            id: list[i].id,
            pId: list[i].pId,
            name: list[i].name,
            open: list[i].open,
            checked: list[i].checked,
            children: []
          }
          temptree[list[i].id] = trow
          items.push(trow)
        }
        if (list[i].uid > 0) {
          temptree[list[i].id]['children'].push({
            id: list[i].id,
            pId: list[i].pId,
            name: list[i].name,
            open: list[i].open,
            checked: list[i].checked,
            children: []
          })
        }
      }

      for (var j in items) {
        if (temptree[items[j].pId]) {
          temptree[items[j].pId]['children'].push(temptree[items[j].id])
        } else {
          tree.push(temptree[items[j].id])
        }
      }
      temptree = null
      items = null
      return tree
    },
    // -
    menuPermSave() {
      let parm = {
        roleId: this.saveroleId,
        moduleIds: ''
      }
      let node = this.$refs.tree.getCheckedNodes()
      let moduleIds = []
      if (node.length != 0) {
        for (let i = 0; i < node.length; i++) {
          moduleIds.push(node[i].id)
        }
        parm.moduleIds = JSON.stringify(moduleIds)
      }
      RoleRightSave(parm)
        .then(res => {
          if (res.success) {
            this.$message({
              type: 'success',
              message: ''
            })
            this.menuAccessshow = false
            this.getdata(this.formInline)
          } else {
            this.$message({
              type: 'info',
              message: res.msg
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.menuAccessshow = false
      }
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>




