// 
import Vue from 'vue';
import Router from 'vue-router';
//
import login from '@/views/login';
// 
import index from '@/views/index';
/**
 * 
 */
// 
import Goods from '@/views/goods/Goods';
// 
import Machine from '@/views/machine/Machine';
// 
import MachineAisle from '@/views/machine/MachineAisle';
/**
 * 
 */
// // 
// import Order from '@/views/pay/Order';
/**
 * 
 */
// 
import AMap from "@/views/amap/AMap";
//
import Monitor from "@/views/amap/Monitor";
import BehavioralSurveillance from "@/views/amap/BehavioralSurveillance";
/**
 * 
 */
// 
import user from '@/views/system/user';
// 
import Module from '@/views/system/Module';
// 
import Role from '@/views/system/Role';
//
import Dept from '@/views/system/Dept';
// 
import Variable from '@/views/system/Variable';
// 
import Permission from '@/views/system/Permission';
/**
 * 
 */
// 
import MachineConfig from '@/views/machine/MachineConfig';
// 
import Config from '@/views/pay/Config';
/**
 * 
 */
// 
import druidLogin from '@/views/druid/login';

// 
import statistics from '@/views/charts/statistics';

// 
Vue.use(Router);

// 
export default new Router({
    routes: [
      {
        path: '/',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/login',
        name: '',
        component: login,
        hidden: true,
        meta: {
            requireAuth: false
        }
    }, {
        path: '/index',
        name: '',
        component: index,
        iconCls: 'el-icon-tickets',
        children: [{
            path: '/goods/Goods',
            name: '',
            component: Goods,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/machine/Machine',
            name: '',
            component: Machine,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/machine/MachineAisle',
            name: '',
            component: MachineAisle,
            meta: {
                requireAuth: true
            }
        },  {
            path: '/system/user',
            name: '',
            component: user,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Module',
            name: '',
            component: Module,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Role',
            name: '',
            component: Role,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Dept',
            name: '',
            component: Dept,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Variable',
            name: '',
            component: Variable,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/system/Permission',
            name: '',
            component: Permission,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/machine/MachineConfig',
            name: '',
            component: MachineConfig,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/pay/Config',
            name: '',
            component: Config,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/druid/login',
            name: '',
            component: druidLogin,
            meta: {
                requireAuth: true
            }
        }, {
            path: '/charts/statistics',
            name: '',
            component: statistics,
            meta: {
                requireAuth: true
            }
        }, {
          path: '/amap/AMap',
          name: '',
          component: AMap,
          meta: {
            requireAuth: true
          }
        },
          {
            path: '/amap/Monitor',
            name: '',
            component: Monitor,
            meta: {
              requireAuth: true
            }
          },
          {
            path: '/amap/BehavioralSurveillance',
            name: '',
            component: BehavioralSurveillance,
            meta: {
              requireAuth: true
            }
          }
          ]
    }]
})



