// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
// element UI
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App';
// 
import router from './router';
// 
import store from './vuex/store';
// icon
import './assets/icon/iconfont.css'
//



import flvjs from 'flv.js';
Vue.use(flvjs)
// echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
// 
import axios from 'axios';
Vue.prototype.$axios = axios;
axios.defaults.baseURL = "http://192.168.29.76:6010"
// axios.defaults.baseURL = "http://192.168.43.216:6010"
// axios.defaults.baseURL = "http://101.34.13.144:6010"
Vue.config.productionTip = false;

// element UI
Vue.use(ElementUI);
// 
import * as custom from './utils/util'

Object.keys(custom).forEach(key => {
    Vue.filter(key, custom[key])
})


//
import VueAMap from 'vue-amap'

Vue.use(VueAMap)
VueAMap.initAMapApiLoader({
  key: 'cecf91ca8164d37af8282ad46ca57230',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PlaceSearch', 'AMap.Geolocation','AMap.Autocomplete', 'AMap.Geocoder'],
  v: '1.4.4',
  uiVersion: '1.0'
})

// 
router.beforeEach((to, from, next) => {
    if (to.matched.length != 0) {
        if (to.meta.requireAuth) { // 
            if (Boolean(localStorage.getItem("userInfo"))) { // vuex stateuser
                next();
            } else {
                next({
                    path: '/login',
                    query: { redirect: to.fullPath } // path
                })
            }
        } else {
            if (Boolean(localStorage.getItem("userInfo"))) { // 
                if (to.path != "/" && to.path != "/login") { //
                    next();
                } else {
                    /**
                     * 
                     */
                    next({
                        path: '/goods/Goods'
                    })
                }
            } else {
                next();
            }
        }
    } else {
        next({
            path: '/login',
            query: { redirect: to.fullPath } // path
        })
    }
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store, //store vuex
    components: { App },
    template: '<App/>',
    data: {
        // 
        Bus: new Vue()
    }

})



